import { goHome } from './links';

const get = (url, headers = {}) => {
  return fetch(url, {
    method: 'GET',
    headers,
  }).then(responce => responce.json());
};

const post = (url, { body, headers = {} }) => {
  return fetch(url, {
    method: 'POST',
    body: JSON.stringify(body),
    headers: {
      Origin: 'https://sw.obpmprod.ch',
      'Content-Type': 'application/json',
      ...headers,
    },
  })
    .then(async data => {
      if (data.status >= 400) {
        if (data.status === 403) {
          goHome();
        }
        throw await data.json();
      }
      return data;
    })
    .then(async data => {
      return data.json();
    })
    .catch(e => {
      throw e;
    });
};

export { get, post };
