import React, { useCallback, useState } from 'react';
import { Checkbox, FormGroup } from '@blueprintjs/core';
import AuthForm from 'components/AuthForm/AuthForm';
import { bool, func, object, oneOfType, string } from 'prop-types';
import { useForm } from 'hooks/useForm';
import './RegisterForm.scss';
import { i18n } from 'i18n';
import Field from '../Field/Field';
import Icon, { ICON_NAMES } from 'components/Icon';
import Button from 'components/Button/Button';
import ErrorMessage from '../ErrorMessage/ErrorMessage';
import Link from 'components/Link/Link';
import { validateFormProps } from 'utils/validators';
import { SIGN_IN } from '../../../routes';

const RegisterForm = ({ register, username, fetching, error, ...props }) => {
  const {
    showPassword,
    handlePassword,
    handleCheckbox,
    handleEmail,
    email,
    togglePasswordDisplay,
    checkbox,
    password,
  } = useForm();
  const [submitted, setSubmitted] = useState(false);

  const handleRegister = useCallback(() => {
    setSubmitted(true);
    if (validateFormProps({ email, password, checkbox })) {
      register({ email: email.value, username, password: password.value });
    }
  }, [register, email, password, checkbox]);

  const isShow = prop =>
    (prop.touched && !prop.isValid) || (submitted && !prop.isValid)
      ? prop.error
      : '';
  return (
    <AuthForm
      title={i18n.t('registerTitle')}
      description={<span>{i18n.t('alreadyHaveRiva')}</span>}
    >
      <Field
        data={email}
        isShowError={isShow}
        fetching={fetching}
        labelFormGroup={i18n.t('email')}
        placeholder={i18n.t('placeholderRegisterEmail')}
        onChangeInputGroup={handleEmail}
        onBlurInputGroup={handleEmail}
      />
      <Field
        type={showPassword ? 'text' : 'password'}
        data={password}
        isShowError={isShow}
        fetching={fetching}
        labelFormGroup={i18n.t('password')}
        placeholder={i18n.t('passwordPlaceholder')}
        onChangeInputGroup={handlePassword}
        onBlurInputGroup={handlePassword}
        rightElement={
          <Button
            className="input-icon"
            icon={
              showPassword ? (
                <Icon name={ICON_NAMES.EYE_PASSWORD_ICON} />
              ) : (
                <Icon name={ICON_NAMES.EYE_PASSWORD_HIDE_ICON} />
              )
            }
            minimal={true}
            onClick={togglePasswordDisplay}
          />
        }
      />

      <FormGroup
        className="agreement-block"
        helperText={submitted && !checkbox.isValid ? checkbox.error : ''}
      >
        <Checkbox
          checked={checkbox.value}
          disabled={fetching}
          onChange={handleCheckbox}
        >
          <span>{i18n.t('agreeWith')} </span>
          <a href="/courses?sort=name">{i18n.t('tAndC')}</a>
          <span> {i18n.t('and')} </span>
          <a href="/courses?sort=name">{i18n.t('priPol')}</a>
        </Checkbox>
      </FormGroup>
      <ErrorMessage error={error} />
      <Button
        className="btn-primary"
        large={true}
        text="register"
        disabled={fetching}
        onClick={handleRegister}
      />
      <div className="additional-actions">
        <span>{i18n.t('alreadyHaveAccount')} </span>
        <Link to={SIGN_IN} preserveQueryString>
          {i18n.t('signInButton')}
        </Link>
      </div>
    </AuthForm>
  );
};

RegisterForm.propTypes = {
  fetching: bool.isRequired,
  register: func.isRequired,
  username: string,
  error: oneOfType([object, string]),
};

RegisterForm.defaultProps = {
  username: '',
};

export default RegisterForm;
