import { useState, useCallback } from 'react';
import validators from '../utils/validators';
import { i18n } from 'i18n';
export const useForm = () => {
  const [username, setUsername] = useState({
    touched: false,
    value: '',
    isValid: false,
    error: i18n.t('emailError'),
    validator: validators.common,
  });
  const [checkbox, setCheckbox] = useState({
    touched: true,
    value: '',
    isValid: false,
    error: i18n.t('checkboxError'),
    validator: validators.checkbox,
  });

  const [email, setEmail] = useState({
    touched: false,
    value: '',
    isValid: false,
    error: i18n.t('emailError'),
    validator: validators.email,
  });
  const [password, setPassword] = useState({
    touched: false,
    value: '',
    isValid: false,
    error: i18n.t('passwordError'),
    validator: validators.password,
  });
  const [showPassword, setShowPassword] = useState(false);

  const togglePasswordDisplay = () => setShowPassword(!showPassword);

  const getReadyState = (e, { validator = () => true }) => {
    const value =
      e.target.type === 'checkbox' ? e.target.checked : e.target.value || '';
    return { value, touched: true, isValid: validator(value) };
  };
  const handleEmail = useCallback(
    e => setEmail({ ...email, ...getReadyState(e, email) }),
    [email, setEmail]
  );
  const handleUsername = useCallback(
    e => setUsername({ ...username, ...getReadyState(e, username) }),
    [username, setUsername]
  );
  const handlePassword = useCallback(
    e => setPassword({ ...password, ...getReadyState(e, password) }),
    [password, setPassword]
  );
  const handleCheckbox = useCallback(
    e => {
      setCheckbox({ ...checkbox, ...getReadyState(e, checkbox) });
    },
    [checkbox, setCheckbox]
  );
  return {
    handleUsername,
    handlePassword,
    handleEmail,
    handleCheckbox,
    username,
    checkbox,
    password,
    email,
    showPassword,
    togglePasswordDisplay,
  };
};
