export const getUsername = () => {
  return window.localStorage.getItem('username') || null;
};

export const getLoginToken = () => {
  return window.localStorage.getItem('loginToken') || null;
};

export const getAccessToken = () => {
  return window.localStorage.getItem('accessToken') || null;
};

export const setUsername = () => {
  return window.localStorage.getItem('username') || null;
};

export const setLoginToken = token => {
  return window.localStorage.setItem('loginToken', token);
};

export const setAccessToken = token => {
  return window.localStorage.setItem('accessToken', token);
};
