import React from 'react';
import { transform, assign, isString, has, get, isUndefined } from 'lodash';
import { Link as LLink } from 'react-router-dom';
import { extract, stringify, parse } from 'query-string';
import { bool, string } from 'prop-types';
import './Link.scss';

export const mergeQueryStrings = (...args) => {
  const queryString = stringify(
    transform(
      args,
      (result, value) => {
        assign(result, isString(value) ? parse(value) : value);
      },
      {}
    )
  );
  return queryString ? '?' + queryString : '';
};

const Link = ({ preserveQueryString, ...props }) => {
  if (isUndefined(preserveQueryString)) {
    preserveQueryString = has(props, 'from');
  }
  if (!preserveQueryString || !location.search) {
    return <LLink {...props} />;
  }

  const { to, ...rest } = props;
  const toSearch = isString(to) ? extract(to) : get(to, 'search', '');
  const search = mergeQueryStrings(location.search, toSearch);
  const nextLocation = isString(to)
    ? { pathname: to.split('?')[0], search }
    : { ...to, search };
  console.log(nextLocation);
  return <LLink to={nextLocation} {...rest} />;
};

Link.propTypes = {
  to: string,
  preserveQueryString: bool,
};
Link.defaultProps = {
  to: '',
  preserveQueryString: true,
};

export default Link;
