import {
  createAction,
  createRequestAction,
  createReducer,
  createRequestTypes,
  createRequestHandlers,
} from 'helpers/reduxHelpers';
import { SHORT_FLOW } from '../constants/flows';
import { DISEASE as result } from '../constants/result';

export const NAME = 'tracker';
const initState = {
  fetching: false,
  step: 1,
  maxSteps: 3,
  flow: SHORT_FLOW,
  result,
};
export const types = {
  GET: createRequestTypes(`${NAME}_GET`),
  SET_FLOW: `${NAME}_SET_FLOW`,
  NEXT_STEP: `${NAME}_NEXT_STEP`,
  PREV_STEP: `${NAME}_PREV_STEP`,
  RESET_STEP: `${NAME}_RESET_STEP`,
  SET_RESULT: `${NAME}_SET_RESULT`,
  SUBMIT: `${NAME}_SUBMIT`,
};

export const actions = {
  get: createRequestAction(types.GET),
  setFlow: createAction(types.SET_FLOW),
  nextStep: createAction(types.NEXT_STEP),
  prevStep: createAction(types.PREV_STEP),
  resetStep: createAction(types.RESET_STEP),
  setResult: createAction(types.SET_RESULT),
  submit: createAction(types.SUBMIT),
};

const handlers = {
  ...createRequestHandlers(types.GET),
  [types.SET_FLOW]: (state, { payload }) => ({
    ...state,
    flow: payload,
    maxSteps: payload === SHORT_FLOW ? 3 : 4,
  }),
  [types.NEXT_STEP]: state => ({
    ...state,
    step: state.maxSteps >= state.step + 1 ? state.step + 1 : state.step,
  }),
  [types.PREV_STEP]: state => ({
    ...state,
    step: state.step - 1 > 0 ? state.step - 1 : state.step,
  }),
  [types.RESET_STEP]: state => ({
    ...state,
    step: 1,
    result: null,
  }),
  [types.SET_RESULT]: (state, { payload }) => ({ ...state, result: payload }),
};

export default createReducer(initState, handlers);
