export const getAccessParams = urlParams => {
  const params = [
    'lang',
    'key',
    'requestingAppId',
    'domain',
    'registerURL',
    'requestedPermissions',
  ];
  return params.reduce(
    (acc, val) => ({ ...acc, [val]: urlParams.get(val) }),
    {}
  );
};

export const getAllParams = location => {
  const urlParams = new URLSearchParams(location.search);
  return getAccessParams(urlParams);
};
