import React from 'react';
import { FormGroup, InputGroup } from '@blueprintjs/core';
import { object, func, bool, string, node } from 'prop-types';
import clsx from 'clsx';
import './Field.scss';

const Field = ({
  data,
  isShowError,
  fetching,
  labelFormGroup,
  placeholder,
  onChangeInputGroup,
  onBlurInputGroup,
  rightElement,
  className,
  type,
}) => (
  <>
    <FormGroup
      className={clsx('user-auth-form', className, {
        'input-danger': isShowError(data),
      })}
      helperText={isShowError(data)}
      label={labelFormGroup}
    >
      <InputGroup
        intent="none"
        type={type}
        disabled={fetching}
        placeholder={placeholder}
        onChange={onChangeInputGroup}
        onBlur={onBlurInputGroup}
        rightElement={rightElement}
      />
    </FormGroup>
  </>
);

Field.propTypes = {
  type: string,
  className: string,
  data: object.isRequired,
  isShowError: func.isRequired,
  fetching: bool.isRequired,
  labelFormGroup: string.isRequired,
  placeholder: string.isRequired,
  onChangeInputGroup: func.isRequired,
  onBlurInputGroup: func.isRequired,
  rightElement: node,
};
Field.defaultProps = {
  className: '',
  type: 'text',
  rightElement: null,
};

export default Field;
