import React from 'react';
import { object, oneOfType, string, func } from 'prop-types';
import './ErrorMessage.scss';
// import { get } from 'lodash';

const ErrorMessage = ({ error }) => {
  if (!error) {
    return null;
  }
  if (typeof error === 'string') {
    return <div className="error-response-message">{error}</div>;
  }
  if (error && error.detail)
    return <div className="error-response-message">{error.detail}</div>;

  if (error && error.error) {
    return <div className="error-response-message">{error.error.message}</div>;
  }
  return null;
};

ErrorMessage.propTypes = {
  error: oneOfType([object, string, func]),
};
ErrorMessage.propTypes = {
  error: null,
};
export default ErrorMessage;
