import {
  createRequestAction,
  createRequestTypes,
  createReducer,
  createRequestHandlers,
} from 'helpers/reduxHelpers';
import { getUsername, getLoginToken } from 'utils/ls';

export const NAME = 'auth';

const initialState = {
  data: [],
  isAuthorized: false,
  fetching: false,
  authorizedData: {},
  uidData: null,
  accessInfo: {},
  loginData: {
    username: getUsername(),
    token: getLoginToken(),
  },
  registerData: {},
  error: null,
};

export const types = {
  LOGIN: createRequestTypes(`${NAME}_LOGIN`),
  REGISTER: createRequestTypes(`${NAME}_REGISTER`),
  CHECK_APP: createRequestTypes(`${NAME}_CHECK_APP`),
  ACCESS_INFO: createRequestTypes(`${NAME}_ACCESS_INFO`),
  CHECK_USERNAME: createRequestTypes(`${NAME}_CHECK_USERNAME`),
  CHECK_UID: createRequestTypes(`${NAME}_CHECK_UID`),
};

export const actions = {
  login: createRequestAction(types.LOGIN),
  register: createRequestAction(types.REGISTER),
  checkApp: createRequestAction(types.CHECK_APP),
  accessInfo: createRequestAction(types.ACCESS_INFO),
  checkUsername: createRequestAction(types.CHECK_USERNAME),
  checkUid: createRequestAction(types.CHECK_UID),
};

export const handlers = {
  ...createRequestHandlers(types.LOGIN, {
    successHandler(state, { payload }) {
      return {
        ...state,
        fetching: false,
        loginData: payload,
      };
    },
  }),
  ...createRequestHandlers(types.REGISTER, {
    successHandler(state, { payload }) {
      return {
        ...state,
        fetching: false,
        registerData: payload,
      };
    },
  }),
  ...createRequestHandlers(types.CHECK_UID, {
    successHandler(state, { payload }) {
      return {
        ...state,
        fetching: false,
        uidData: payload,
      };
    },
  }),
  ...createRequestHandlers(types.CHECK_USERNAME, {
    successHandler(state, { payload }) {
      return {
        ...state,
        fetching: false,
        usernameData: payload,
      };
    },
  }),
  ...createRequestHandlers(types.CHECK_APP, {
    successHandler(state, { payload }) {
      return {
        ...state,
        fetching: false,
        authorizedData: payload,
      };
    },
  }),
  ...createRequestHandlers(types.ACCESS_INFO, {
    successHandler(state, { payload }) {
      return {
        ...state,
        fetching: false,
        accessInfo: payload,
      };
    },
  }),
};

export default createReducer(initialState, handlers);
