import React from 'react';
import { bool, string, func } from 'prop-types';
import './Button.scss';
import { Button as ButtonBP } from '@blueprintjs/core';
import clsx from 'clsx';
import './Button.scss';
import { i18n } from 'i18n';

const Button = ({
  upperCase,
  loading,
  onClick,
  text,
  large,
  className,
  ...props
}) => (
  <ButtonBP
    {...props}
    large={large}
    loading={loading}
    minimal={true}
    className={clsx('btn', className, { upper_case: upperCase })}
    active={false}
    text={i18n.t(text)}
    onClick={onClick}
  />
);
Button.propTypes = {
  ...ButtonBP.propTypes,
  upperCase: bool,
  loading: bool,
  className: string,
  onClick: func.isRequired,
  text: string,
  large: bool,
};
Button.defaultProps = {
  upperCase: false,
  large: true,
  text: '',
  className: '',
  loading: false,
};
export default Button;
