import React from 'react';
import { node, string, bool } from 'prop-types';
import './AuthForm.scss';
import Icon, { ICON_NAMES } from 'components/Icon';
import clsx from 'clsx';

const AuthForm = ({
  title,
  description,
  children,
  withIcon,
  descriptionClassName,
}) => {
  return (
    <div className="wrapper">
      <div className="auth-form">
        <h1 className="auth-form__title">{title}</h1>
        <div className={clsx('auth-form__description', descriptionClassName)}>
          {withIcon && <Icon name={ICON_NAMES.SMALL_LOGO_ICON} />}
          <div>{description}</div>
        </div>
        <div className="user-form-container">{children}</div>
      </div>
    </div>
  );
};

AuthForm.propTypes = {
  title: string,
  description: node,
  children: node,
  withIcon: bool,
  descriptionClassName: string,
};
AuthForm.defaultProps = {
  title: '',
  description: null,
  children: null,
  withIcon: true,
  descriptionClassName: '',
};
export default AuthForm;
