import React, { useCallback } from 'react';
import { get } from 'lodash';
import { i18n } from 'i18n';
import AuthForm from 'components/AuthForm/AuthForm';
import Button from 'components/Button/Button';
import './Grant.scss';
import { object } from 'prop-types';

const Grant = ({ params }) => {
  const requestedPermissions = get(params, 'requestedPermissions');
  if (!requestedPermissions) return null;

  const parsedRP = JSON.parse(requestedPermissions);
  const DescrioptionGrant = ({ ...rest }) => {
    return (
      <div>
        <span>{i18n.t('descriptionGrantText')}</span>
        <ul>
          {parsedRP.map((val, key) => (
            <li key={key}>
              {[
                i18n.t('accessTo'),
                i18n.t(val.level),
                i18n.t('your'),
                val.defaultName,
              ].join(' ')}
            </li>
          ))}
        </ul>
      </div>
    );
  };
  const handlerAccept = useCallback(() => {}, []);
  const handlerReject = useCallback(() => {}, []);
  return (
    <AuthForm
      withIcon={false}
      title={i18n.t('grantTitle')}
      description={<DescrioptionGrant />}
    >
      <div className="button-block row">
        <Button className="btn-danger" text="accept" onClick={handlerReject} />
        <Button className="btn-primary" text="reject" onClick={handlerAccept} />
      </div>
    </AuthForm>
  );
};

Grant.propTypes = { params: object.isRequired };

export default Grant;
