import { getUsername, getLoginToken } from './ls';
import { isEmpty } from 'lodash';
import { goHome } from './links';

export const isEmail = str => str.includes('@');
export const getUsernameFromEmail = str => str.split('@')[0];

export const isAuthorized = () => {
  const username = getUsername();
  const loginToken = getLoginToken();
  return !isEmpty(username) && !isEmpty(loginToken);
};

export const checkCredentialsOrRedirect = () => {
  if (!isAuthorized()) {
    goHome();
  }
};
