import { takeLatest, select } from 'redux-saga/effects';
import getProfileAndChronicDiseases from '../selectors/getProfileAndChronicDiseases';
import { types } from '../reducers/profile';

function* submit() {
  try {
    const { chronicDiseases, profile } = yield select(
      getProfileAndChronicDiseases
    );
    console.warn({
      chronicDiseases,
      profile,
    });
  } catch (e) {
    console.warn(e);
  }
}

export default function* sagas() {
  yield takeLatest(types.SUBMIT, submit);
}
