import { connect } from 'react-redux';
import LoginForm from 'modules/auth/components/LoginForm/LoginForm';
import { NAME, actions } from 'modules/auth/reducers/auth';
import { getAllParams } from 'utils/query';

export default connect(
  ({ [NAME]: { data, fetching, error }, router: { location } }) => {
    return {
      data,
      error,
      fetching,
      params: getAllParams(location),
    };
  },
  dispatch => ({
    login: payload => dispatch(actions.login.request(payload)),
  })
)(LoginForm);
