import { connect } from 'react-redux';
import { flow } from 'lodash';
import RegisterForm from 'modules/auth/components/RegisterForm/RegisterForm';
import { NAME, actions } from 'modules/auth/reducers/auth';
import { getAllParams } from 'utils/query';
import withOnMount from 'hocs/withOnMount';
import { get } from 'lodash';

const connector = connect(
  ({
    [NAME]: { data, fetching, error, usernameData },
    router: { location },
  }) => ({
    data,
    fetching,
    error,
    username: get(usernameData, 'username'),
    params: getAllParams(location),
  }),
  (dispatch, ...rest) => ({
    onMount: () => dispatch(actions.checkUsername.request()),
    register: payload => dispatch(actions.register.request(payload)),
  })
);

export default flow([withOnMount, connector])(RegisterForm);
