import React, { useCallback, useState } from 'react';
import AuthForm from 'components/AuthForm/AuthForm';
import { useForm } from 'hooks/useForm';
import { func, bool, object } from 'prop-types';
import './LoginForm.scss';
import { i18n } from 'i18n';
import Field from '../Field/Field';
import Icon, { ICON_NAMES } from 'components/Icon';
import Button from 'components/Button/Button';
import ErrorMessage from '../ErrorMessage/ErrorMessage';
import Link from 'components/Link/Link';
import { validateFormProps } from 'utils/validators';
import { SIGN_UP } from '../../../routes';

const LoginForm = ({ login, fetching, error }) => {
  const {
    showPassword,
    handlePassword,
    handleUsername,
    togglePasswordDisplay,
    username,
    password,
  } = useForm();
  const [submitted, setSubmitted] = useState(false);

  const handleLogin = useCallback(() => {
    setSubmitted(true);
    if (validateFormProps({ username, password })) {
      login({ username: username.value, password: password.value });
    }
  }, [login, username, password]);

  const isShow = prop =>
    (prop.touched && !prop.isValid) || (submitted && !prop.isValid)
      ? prop.error
      : '';
  return (
    <AuthForm
      title={i18n.t('signInTitle')}
      description={<span>{i18n.t('alreadyHaveRiva')}</span>}
    >
      <Field
        data={username}
        isShowError={isShow}
        fetching={fetching}
        labelFormGroup={i18n.t('emailOrUserId')}
        placeholder={i18n.t('emailPlaceholder')}
        onChangeInputGroup={handleUsername}
        onBlurInputGroup={handleUsername}
      />
      <Field
        className="mb-16"
        type={showPassword ? 'text' : 'password'}
        data={password}
        isShowError={isShow}
        fetching={fetching}
        labelFormGroup={i18n.t('password')}
        placeholder={i18n.t('passwordPlaceholder')}
        onChangeInputGroup={handlePassword}
        onBlurInputGroup={handlePassword}
        rightElement={
          <Button
            className="input-icon"
            icon={
              showPassword ? (
                <Icon name={ICON_NAMES.EYE_PASSWORD_ICON} />
              ) : (
                <Icon name={ICON_NAMES.EYE_PASSWORD_HIDE_ICON} />
              )
            }
            minimal={true}
            onClick={togglePasswordDisplay}
          />
        }
      />
      <div className="forgot-password-block">
        <Link
          className="forgot-password-link"
          to="/courses?sort=name"
          preserveQueryString
        >
          {i18n.t('forgotPassword')}
        </Link>
      </div>
      <ErrorMessage error={error} />

      <Button
        className="btn-primary"
        disabled={fetching}
        text="signInButton"
        onClick={handleLogin}
      />
      <div className="additional-actions">
        <span>{i18n.t('dontHaveAccount')}</span>
        <Link to={SIGN_UP} preserveQueryString>
          {i18n.t('registerHere')}
        </Link>
      </div>
    </AuthForm>
  );
};

LoginForm.propTypes = {
  login: func.isRequired,
  fetching: bool.isRequired,
  error: object,
};

LoginForm.defaultProps = {
  error: null,
};

export default LoginForm;
