import FormFactory from 'helpers/formFactory';
import { createAction } from 'helpers/reduxHelpers';

export const NAME = 'profile';

const initState = {
  fetching: false,
  data: {
    birthday: null,
    gender: 'male',
    height: {},
    weight: {},
    zip: '',
  },
};

const store = FormFactory(NAME, initState);
const { actions: factoryActions, reducer, types: factoryTypes } = store;

const types = {
  ...factoryTypes,
  SUBMIT: `${NAME}_REGISTRATION`,
};

const actions = {
  ...factoryActions,
  submit: createAction(types.SUBMIT),
};
export { actions, reducer, types };
export default { ...store, actions, types };
