import { combineReducers } from 'redux';
import { routerReducer as router } from './history';
import auth, { NAME as AUTH_NAME } from './auth/reducers/auth';
import feelHealthy from './SymptomTracker/reducers/feelHealthy';
import symptoms from './SymptomTracker/reducers/symptoms';
import chronicDiseases from './SymptomTracker/reducers/chronicDiseases';
import coronaQuestions from './SymptomTracker/reducers/coronaQuestions';
import profile from './Profile/reducers/profile';

import tracker, {
  NAME as TRACKER_NAME,
} from './SymptomTracker/reducers/tracker';

export const rootReducer = combineReducers({
  router,
  [AUTH_NAME]: auth,
  [TRACKER_NAME]: tracker,
  [feelHealthy.NAME]: feelHealthy.reducer,
  [symptoms.NAME]: symptoms.reducer,
  [chronicDiseases.NAME]: chronicDiseases.reducer,
  [coronaQuestions.NAME]: coronaQuestions.reducer,
  [profile.NAME]: profile.reducer,
});
