import React from 'react';
import { Provider } from 'react-redux';
import { ConnectedRouter } from 'connected-react-router';
import { Route } from 'react-router-dom';
import { store } from './store';
import { history } from './history';
import Login from './auth/containers/Login/Login';
import Register from './auth/containers/Register/Register';
import Grant from './auth/containers/Grant/Grant';
import '../i18n';
import Wrapper from './auth/containers/Wrapper/Wrapper';
import { ROOT, SIGN_IN, GRANT, SIGN_UP } from './routes';

export const App = () => (
  <Provider store={store}>
    <ConnectedRouter history={history}>
      <Wrapper>
        <Route path={ROOT} exact component={Login} />
        <Route path={SIGN_IN} component={Login} />
        <Route path={GRANT} exact component={Grant} />
        <Route path={SIGN_UP} component={Register} />
      </Wrapper>
    </ConnectedRouter>
  </Provider>
);
